import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout"


export default ({ data }) => {
  const badge = data.pluralsightJson
  console.log("TCL: badge", badge)
  return (
    <Layout>
      <div>
        <h1>{badge.title}</h1>
        <div>{badge.score}</div>
        <div>{Math.floor(badge.percentile)}%</div>
        <div>{badge.dateCompleted}</div>
        <div>{badge.level}</div>
        <Img
          fluid={badge.localImage.childImageSharp.fluid}
          alt={badge.title}
          style={{ float: "left", marginRight: "1rem", width: 150 }}
        />
      </div>
    </Layout>
  )
}

export const query = graphql`
  query($badge: String!) {
    pluralsightJson(id: { eq: $badge }) {
      percentile
      title
      dateCompleted(fromNow: true)
      level
      thumbnailUrl
      localImage {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      score
    }
  }
`